import { useParams, Link } from 'react-router-dom';
import './Player.css'; 
import BackBar from './component/BackBar';
import { encryptText } from './util/encryptionUtils';
import { playerDataState, nextLinkState, refreeNameState } from './util/recoild';
import { useRecoilState, useRecoilValue } from 'recoil';
import { getRoundDescription } from './util/getRoundDescription';
