  const { api, to_cd, phone_number, detail_class_cd, rh_cd, refree, category } = useParams();
  const [playerData, setPlayerData] = useState([]);
  const [recoilPlayerData, setRecoilPlayerData] = useRecoilState(playerDataState); // Recoil  
