// getKindDescription.js

export function getKindDescription(code) {
    //    
    if (typeof code === 'string') {
        code = parseInt(code, 10);
    }

    switch (code) {
        case 1: return '';
        case 2: return '';
        case 3: return '18 ';
        case 4: return '3';
        case 5: return '';
        case 6: return '';
        case 7: return '63 ';
        case 8: return '64 ';
        default:
            return 'Unknown Kind';
    }
}
