import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import './NsrResult.css'; 
import BackBar from './component/BackBar';
import { useRecoilValue } from 'recoil';
import { playerDataState, refreeNameState } from './util/recoild';
import useOrientation from './util/useOrientation'; //   
import { getRoundDescription } from './util/getRoundDescription';
import { getKindDescription } from './util/getKindDescription';




function NsrResult() {
  const isPortrait = useOrientation(); //    
  const navigate = useNavigate(); //   
  const { api, to_cd, phone_number, detail_class_cd, rh_cd, refree, category, index} = useParams();
  /*
  api:   . pc localhost,   pc ip
  to_cd:  // detail_class_cd:  // rhcd:   ( -    - )
  phone_number: tour_refree  MOBILE_NO .  
  refree: (1), (2,3) (4,5) (6,7)
  category: (1), (2), (3) --   refree 2  
  index: n  or .  playerData  .
   */
  const kind_cd = Math.floor(detail_class_cd % 1000 / 100);
  // kincd_cd = (1)  (2) -> detail_class_cd = 89202 = 89 2 02 =  '' 
  const playerData = useRecoilValue(playerDataState);
  //  or     . 
  const [kor_nm, set_kor_nm] = useState(""); 
  //playerData[index].NM -> index  state 
  const [entrant_team_id, set_entrant_team_id] = useState(-7); 
  //playerData[index].ID -> 
  const newCategory = refree === '1' ? category : parseInt(refree / 2);


  const [prev, setPrev] = useState({
    index: index > 0 ? Number(index) - 1 : null,
    link: 0,
    name: ""
  });

  const [next, setNext] = useState({
    index: index < playerData.length - 1 ? Number(index) + 1 : null,
    link: 0,
    name: ""
  });
  //,     .
  //playerData[index]  ,    ,  plyaerData[index].NM 

  const updateNext = (newData) => {
    setNext((prevUser) => ({
      ...prevUser,
      ...newData
    }));
  };
  const updatePrev = (newData) => {
    setPrev((prevUser) => ({
      ...prevUser,
      ...newData
    }));
  };

  const [isSave, setIsSave] = useState(0); //  
  const [maxCount, setMaxCount] = useState(0); //     (12/5/6) -> 12 (    )
  const [maxPoint, setMaxPoint] = useState(0); //    (6) -> 1 2 3 4 5 6   
  const [allPoint, setAllPoint] = useState(0); //   (40) -> 32/40  

  //fetch-nsrresult  
  const [markData, setMarkData] = useState([]); //  
  const [mark_base_id, set_mark_base_id] = useState("0"); //    
  const [selectedCells, setSelectedCells] = React.useState({}); //   (= )
  const [mark_seq, set_mark_seq] = useState("1"); //     ex) 12 5 4 
  //def   state   .  


  //get-nsrresult_cnt . (  api .   )
  const [teamCount, setTeamCount] = useState(0); //   

  const refreeName = useRecoilValue(refreeNameState);

  useEffect(() => { //     
    window.scrollTo(0, 0);
  }, [entrant_team_id, index]);

  useEffect(() => { // playerData    
    set_entrant_team_id(playerData[index].ID);
    set_kor_nm(playerData[index].NM);

    const prevIndex1 = index > 0 ? Number(index) - 1 : null; //   
    const nextIndex1 = index < playerData.length - 1 ? Number(index) + 1 : null; //   
    updatePrev({index: prevIndex1});
    updateNext({index: nextIndex1});

    if(prevIndex1 !== null){
      const prevRhcd = playerData[prevIndex1].rh_cd;
      updatePrev({rh_cd: prevRhcd});
      updatePrev({link:`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${prevRhcd}/${refree}/${newCategory}/${prevIndex1}`});
      updatePrev({name: playerData[prevIndex1].NM});
    }
    if(nextIndex1 !== null){
      const nextRhcd = playerData[nextIndex1].rh_cd;
      updateNext({rh_cd: nextRhcd});
      updateNext({link:`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${nextRhcd}/${refree}/${newCategory}/${nextIndex1}`});
      updateNext({name: playerData[nextIndex1].NM});
    }

  }, [playerData, index, category]);
  //       playerData     .

  useEffect(() => {
    if(entrant_team_id === -7){return;} //  recoil playerData    -  

    const getScoreColumn = (refree, category) => { //    db  
      if (refree === '1') { //
        switch (parseInt(category)) {
          case 1: return 'MARS_JP1';
          case 2: return 'MARS_JP4';
          case 3: return 'MARS_JP7';
          default:
            return 0;
        }
      }
      switch (parseInt(refree)) {
        case 2: return 'MARS_JP2'; //
        case 3: return 'MARS_JP3';
        case 4: return 'MARS_JP5'; //
        case 5: return 'MARS_JP6';
        case 6: return 'MARS_JP8'; //
        case 7: return 'MARS_JP9';
        default:
          return 0;
      }
    };
    const mars = getScoreColumn(refree, category);
    const jid_cnt = "JID_CNT"+refree;
    
    const fetchNsrResult = async () => {
      try {
        const response = await fetch(`http://${api}:4000/fetch-nsrresult`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            to_cd,
            detail_class_cd,
            rh_cd,
            entrant_team_id,
            refree,
            newCategory,
            mars,
            jid_cnt
          }),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();
        set_mark_base_id(Number(data.markBaseID)); //  ID
        setMarkData(data.markTable);               //  
        set_mark_seq(data.markTableCount);         //    
        let masCount = 1; // maxCount state          

        if (category === '1') {
          setMaxCount(Number(data.markTableCount.ITEM_CNT1_TEC));
          masCount = Number(data.markTableCount.ITEM_CNT1_TEC);
        } else if (category === '2') {
          setMaxCount(Number(data.markTableCount.ITEM_CNT2_ART));
          masCount = Number(data.markTableCount.ITEM_CNT2_ART);
        } else if (category === '3') {
          setMaxCount(Number(data.markTableCount.ITEM_CNT3_CMP));
          masCount = Number(data.markTableCount.ITEM_CNT3_CMP);
        }

        const score = Number(Object.values(data.totalScore));
        const updatedSelectedCells = {};
        data.seqScore.forEach(row => {
          updatedSelectedCells[row.SEQ] = Number(row.SCORE);
        });
        setSelectedCells(updatedSelectedCells);
        if (data.seqScore.length === 0) {
          try {
            const putResponse = await fetch(`http://${api}:4000/def-nsrresult_mark/${to_cd}/${kind_cd}/${detail_class_cd}/${rh_cd}/${entrant_team_id}/${mark_base_id}/${refree}/${newCategory}`, {
              method: 'PUT',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                maxCount: masCount
              }),
            });
            const result = await putResponse.json();
          } catch (error) {
            console.error('Error calling def-nsrresult_mark API:', error);
          }
        }
        if(data.scoreMatch){setIsSave(1);}
        if(!data.scoreMatch){setIsSave(0);}

      } catch (error) {
        console.error('Error fetching NSR result data:', error);

      }
    };

    fetchNsrResult();
  }, [api, to_cd, detail_class_cd, rh_cd, entrant_team_id, refree, newCategory]);


  useEffect(() => { //   
    if(detail_class_cd %2 !== 0){return;}
    const fetchData = async () => {
      try {
        const response = await fetch(`http://${api}:4000/get-nsrresult_player_cnt/${to_cd}/${kind_cd}/${detail_class_cd}/${rh_cd}/${entrant_team_id}/JID_CNT${refree}`);
        const data = await response.json();
        const tc = Number(Object.values(data));
        if(tc === -1){
          try {
            const response = await fetch(`http://${api}:4000/update-nsrresult_player_cnt/${to_cd}/${kind_cd}/${detail_class_cd}/${rh_cd}/${entrant_team_id}`, {
              method: 'PUT',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                jid: `JID_CNT${refree}`, 
                count: 0,                   
              }),
            });
      
            if (!response.ok) {
              throw new Error('Failed to save nsrresult_teamplayer_count');
            }
          } catch (error) {
            console.error('Error saving nsrresult:', error);
          }
        }
        else{setTeamCount(tc)}
        
      } catch (error) {
        console.error('Error fetching mark content data:', error);
      }
    };

    fetchData();
  }, [entrant_team_id, newCategory]);


  useEffect(() => { //    
    const getMaxPoint = (data) => {
      return data.reduce((max, item) => (item.MAX_POINT > max ? item.MAX_POINT : max), 0);
    };

    const calculateTotalMaxPoint = (data) => {
      return data.reduce((total, item) => total + parseInt(item.MAX_POINT), 0);
    };

    setMaxPoint(getMaxPoint(markData));
    setAllPoint(calculateTotalMaxPoint(markData));
  }, [markData]);







  const getCategory = (category) => { //   
    switch (parseInt(category)){
      case 1: return "";
      case 2: return "";
      case 3: return "";
      default: return 0;
    }
  };

  const handleScoreSave = async () => { //   db  
    try {
      const getScoreColumn = (refree, category) => { //    db  
        if (refree === '1') { //
          switch (parseInt(category)) {
            case 1: return 'MARS_JP1';
            case 2: return 'MARS_JP4';
            case 3: return 'MARS_JP7';
            default:
              return 0;
          }
        }
        switch (parseInt(refree)) {
          case 2: return 'MARS_JP2'; //
          case 3: return 'MARS_JP3';
          case 4: return 'MARS_JP5'; //
          case 5: return 'MARS_JP6';
          case 6: return 'MARS_JP8'; //
          case 7: return 'MARS_JP9';
          default:
            return 0;
        }
      };
  
      const response = await fetch(`http://${api}:4000/update-nsrresult/${to_cd}/${detail_class_cd}/${rh_cd}/${entrant_team_id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          mars: getScoreColumn(refree, category),  //   column  =   
          score: calculateTotal(),                   //  
        }),
      });
  
      if (!response.ok) {
        throw new Error('Failed to save nsrresult');
      }
    } catch (error) {
      console.error('Error saving nsrresult:', error);
    }
  };
  
  const handleSepSave = async () => { //     
    try {
      for (const [seq, score] of Object.entries(selectedCells)) {
        const data = {
          seq: seq, 
          score: -1 //    server null 
        };
  
        // API  .
        const response = await fetch(`http://${api}:4000/update-nsrresult_mark/${to_cd}/${kind_cd}/${detail_class_cd}/${rh_cd}/${entrant_team_id}/${mark_base_id}/${refree}/${newCategory}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(data),
        });
  
        //     .
        if (!response.ok) {
          throw new Error(`Failed to save nsrresult_mark for seq ${seq}`);
        }
      }
    } catch (error) {
      console.error('Error updating nsrresult_mark data:', error);
    }
  };

  const handleCountSave = async () => { //   db 
    try {
      const response = await fetch(`http://${api}:4000/update-nsrresult_player_cnt/${to_cd}/${kind_cd}/${detail_class_cd}/${rh_cd}/${entrant_team_id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          jid: `JID_CNT${refree}`, 
          count: teamCount,                   
        }),
      });
      if (!response.ok) {
        throw new Error('Failed to save nsrresult_teamplayer_count');
      }
    } catch (error) {
      console.error('Error saving nsrresult:', error);
    }
  };

  const handleSave = async () => { //    
    if(detail_class_cd%2===0 && teamCount===0){window.confirm('  .'); return;}
    const numericValuesCount = Object.values(selectedCells).filter(value => typeof value === 'number' && value > 0).length;
    if(numericValuesCount !== maxCount){window.confirm('   .'); return;}

    const isConfirmed = true;
    //if(detail_class_cd%2!==0){isConfirmed = window.confirm(`  ${calculateTotal()} ?`);}
    //else{isConfirmed = window.confirm(`  ${calculateTotal()},   ${teamCount} ?`);}

    if (isConfirmed) {
      try {
        await handleScoreSave();
        //await handleSepSave();
        if(detail_class_cd %2 === 0){
          await handleCountSave();
        }
      } catch (error) {
        console.error('Error saving or counting scores:', error);
        window.confirm('    .  .');
      }
      window.confirm('  .');
      setIsSave(1);
    }
    else{ return;}
  };

  const handleSepSaveOne = async (index, value) => {
    try {
      const seq = index;
      const score = value;
  
      const data = {
        seq: seq,
        score: score
      };
  
      // API  .
      const response = await fetch(`http://${api}:4000/update-nsrresult_mark/${to_cd}/${kind_cd}/${detail_class_cd}/${rh_cd}/${entrant_team_id}/${mark_base_id}/${refree}/${newCategory}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });
  
      //     .
      if (!response.ok) {
        throw new Error(`Failed to save nsrresult_mark for seq ${seq}`);
      }

    } catch (error) {
      console.error('Error updating nsrresult_mark data:', error);
    }
  };







  const calculateTotal = () => { //  
    return Object.values(selectedCells).reduce(
      (total, value) => total + (value ? value : 0),
      0
    );
  };

  const handleReset = async () => {  //   
    if(calculateTotal() === 0) {return ;}
    const isConfirmed = window.confirm("    ?");
    if (isConfirmed) {
      setSelectedCells({}); 
      setIsSave(0);
      try{
        await handleSepSave();
      }
      catch{

      }
    }
  };

  const handleResetMove = (nextLink) => {  //  + 
    if(!isSave) {
      const isConfirmed = window.confirm("   . ?");
      if (isConfirmed) {
        setSelectedCells({});
      }
      else{ return;}
    }
    if (nextLink) {
      navigate(nextLink);
    }
  };





  const handleCellClick = (rowId, value) => { //    
  
    if (value === '-' || (value === 0.5 && (!selectedCells[rowId] || selectedCells[rowId] === Number(markData[rowId-1].MAX_POINT)))) {
      return;
    } // '-'   + 0.5     (     )  
    setIsSave(0);
    let newSelectedCells = { ...selectedCells };
    if (value === 0.5) {
      newSelectedCells[rowId] = selectedCells[rowId] % 1 === 0.5 ? selectedCells[rowId] - 0.5 : selectedCells[rowId] + 0.5;
      value = newSelectedCells[rowId];
    } else {
      newSelectedCells[rowId] = value === Math.floor(selectedCells[rowId]) ? null : value;
      value = newSelectedCells[rowId];
    }
  
    setSelectedCells(newSelectedCells);
  
    if(value !== null && value !== undefined){handleSepSaveOne(rowId, value);}
    else{handleSepSaveOne(rowId, -1);}
  };
  

  const handlePlusClickPlus = () => { //  
    setTeamCount(prevValue => prevValue + 1);
    setIsSave(0);
  };

  const handlePlusClickMinus = () => {
    if(teamCount<=0){return;} // 
    setTeamCount(prevValue => prevValue - 1);
    setIsSave(0);
  };

  const categories = Array.from(new Set(markData.map(item => item.CATEGORY)));

  const generateCellValueArray = (getMaxpoint, currentMaxPoint) => {
    const cellValues = [];
    for (let i = 1; i <= currentMaxPoint; i++) {
      cellValues.push(i);
    }
    for (let i = currentMaxPoint + 1; i <= getMaxpoint; i++) {
      cellValues.push('-');
    }
    if(category !== '3'){
      cellValues.push(0.5);
    }
    
    return cellValues;
  };




  if (!isPortrait) {
    return (
      <div className="nsr-result-container">
      <BackBar 
  url={`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/${newCategory}`}
  text={`${refreeName}  // ${getKindDescription(kind_cd)} ${getRoundDescription(rh_cd)}`}
  sentialDigit={!isSave}
/>

      <h1>{kor_nm}</h1>

      {/*  .   */}
      {detail_class_cd %2 === 0 && 
        <div><table><tbody><tr>
              <td> </td>
              <td>
                <input 
                  type="number" value={teamCount} onChange={(e) => setTeamCount(parseInt(e.target.value))} 
                  onKeyPress={(e) => { if (isNaN(parseInt(e.key))) { e.preventDefault(); }}}
                  className="input-field"
                />
              </td>
              <td>
                <button onClick={handlePlusClickPlus}>+1</button>
              </td>
              <td>
                <button onClick={handlePlusClickMinus}>-1</button>
              </td>
            </tr>
          </tbody>
        </table>
        <div className = "empty_space"></div>
        </div>
      }

      {/*  .   */}
      {refree === '1' && (
        <table>
          <tbody>
            <tr>
              <td onClick={()=>handleResetMove(`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/1/${index}`)} className={category == 1 ? 'selectedk' : ''}>
                  
              </td>
              <td onClick={()=>handleResetMove(`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/2/${index}`)} className={category == 2 ? 'selectedk' : ''}>
                  
              </td>
              <td onClick={()=>handleResetMove(`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/3/${index}`)} className={category == 3 ? 'selectedk' : ''}>
                  
              </td>
            </tr>
          </tbody>
        </table>
      )}

        <div className="table-container">
          <table className="mark-table">
            <thead>
              <tr>
                <th style={{width:'10%'}}></th>
                <th style={{width:'80%'}}></th>

              </tr>
            </thead>
            <tbody>
              <tr>
                <td className='text-center'>{getCategory(category)} <br></br> ({allPoint})</td>

                <td>
                {categories.map((category, catIndex) => {
                  const categoryItems = markData.filter(item => item.CATEGORY === category);
                  const rowSpan = categoryItems.length;

                  return categoryItems.map((item, index) => (
                    <tr key={`${category}-${index}`}>
                      {categories.length !== 1 && index === 0 && (
                        <td rowSpan={rowSpan} className='category'>
                          {category} <br /> ({categoryItems.reduce((acc, cur) => acc + Number(cur.MAX_POINT), 0)})
                        </td>
                      )}
                      <td className='content'>{item.MARK_CONTENT}</td>
                      <td className='score-cells d' style={{ padding: 0, margin: 0 }}>
                        {generateCellValueArray(maxPoint, item.MAX_POINT).map((value, idx) => (
                          <td 
                            className={`score-cell
                              ${Math.floor(selectedCells[item.SEQ_NO]) === value ? 'selected' : ''} 
                              ${ ( value === 0.5 && selectedCells[item.SEQ_NO]%1===0.5 ) === true ? 'selected' : ''}`}
                            onClick={()=>handleCellClick(item.SEQ_NO, value)}>
                            {value}
                          </td>
                        ))}
                     </td>
                     {categories.length !== 1 && index === 0 && (
                      <td rowSpan={rowSpan} className='category'>
                         {categoryItems.reduce((acc, cur) => acc + (selectedCells[Number(cur.SEQ_NO)] || 0), 0)}
                      </td>
                      )}
                     {categories.length === 1 && ( <>
                      <td></td>
                      <td className='score-sans'>
                        {selectedCells[item.SEQ_NO] || 0}
                      </td>
                      </>
                      )}
                      

                    </tr>
                  ));
                })}
                </td>
              </tr>
              <div className = "empty_space"></div>

<tr className="total-row">
  <td colSpan={11}>
     : {calculateTotal()}
    { (isSave === 1) &&  " ()"}
    {( isSave=== 0 ) && " ( )"}
  </td>
</tr>
</tbody>
          </table>
          </div>

          <table>
  <tbody>
    <tr className="button-row">
      <td colSpan={2} className="button-container1">
        <button onClick={handleSave} className="save-button">
          
        </button>
        <button onClick={handleReset} className="reset-button">
          
        </button>
      </td>
    </tr>
  </tbody>
</table>
        <div className = "empty_space"></div>
        <div className = "empty_space"></div>
        <div className = "empty_space"></div>
        <table>
  <tbody>
    <tr>
      <td colSpan={2} className="button-container2">
        { ( prev.index !== null && (rh_cd === prev.rh_cd || rh_cd !== '899000') ) ? (
          <button
          onClick={handleResetMove.bind(
            null,
            prev.link
          )}
            className="nav-button"
          >
            <div> {parseInt(detail_class_cd) % 2 === 0 ? "" : ""}</div>
            {prev.name} ({getRoundDescription(prev.rh_cd)})
          </button>
        ) : (
          <span></span>
        )}
        {( next.index !== null && (rh_cd === next.rh_cd || next.rh_cd !== '899000'))? (
          <button
            onClick={handleResetMove.bind(
              null,
              next.link
            )}
            className="nav-button"
          >
            <div> {parseInt(detail_class_cd) % 2 === 0 ? "" : ""}</div>
            {next.name} ({getRoundDescription(next.rh_cd)})

          </button>
        ) : (
          <span></span>
        )}
      </td>
    </tr>
  </tbody>
</table>
<div className = "empty_space"></div>
<div className = "empty_space"></div>

      </div>
    );
  }


  return (
    <div className="nsr-result-container">
      
      <BackBar 
  url={`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/${newCategory}`}
  text={`${refreeName}  // ${getKindDescription(kind_cd)} ${getRoundDescription(rh_cd)}`}
  sentialDigit={!isSave}
/>

      <h1>{kor_nm}</h1>

      {/*  .   */}
      {detail_class_cd %2 === 0 && 
        <div><table><tbody><tr>
              <td> </td>
              <td>
                <input 
                  type="number" value={teamCount} onChange={(e) => setTeamCount(parseInt(e.target.value))} 
                  onKeyPress={(e) => { if (isNaN(parseInt(e.key))) { e.preventDefault(); }}}
                  className="input-field"
                />
              </td>
              <td>
                <button onClick={handlePlusClickPlus}>+1</button>
              </td>
              <td>
                <button onClick={handlePlusClickMinus}>-1</button>
              </td>
            </tr>
          </tbody>
        </table>
        <div className = "empty_space"></div>
        </div>
      }

      {/*  .   */}
      {refree === '1' && (
        <table>
          <tbody>
            <tr>
              <td onClick={()=>handleResetMove(`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/1/${index}`)} className={category == 1 ? 'selectedk' : ''}>
                                    
              </td>
              <td onClick={()=>handleResetMove(`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/2/${index}`)} className={category == 2 ? 'selectedk' : ''}>
                  
              </td>
              <td onClick={()=>handleResetMove(`/${api}/${to_cd}/${phone_number}/${detail_class_cd}/${rh_cd}/${refree}/3/${index}`)} className={category == 3 ? 'selectedk' : ''}>
                  
              </td>
            </tr>
          </tbody>
        </table>
      )}

<div className = "empty_space"></div>
      {markData.length > 0 ? (<>
        <table className="nsr-result-table">
          <thead>
            <tr>
              <th colSpan = {777} className = "header-cell">
                {getCategory(category)} ({allPoint})
              </th>
            </tr>
          </thead>
          <tbody>
            {category === '1' && <div className = "empty_space"></div> }
                {categories.map((category, catIndex) => {
                  const categoryItems = markData.filter(item => item.CATEGORY === category);
                  const rowSpan = categoryItems.length;

                  return categoryItems.map((item, index) => (<>
                      {categories.length !== 1 && index === 0 && (<tr>
                        <td className='category'>
                          {category} <br /> ( {categoryItems.reduce((acc, cur) => acc + (selectedCells[Number(cur.SEQ_NO)] || 0), 0)} / {categoryItems.reduce((acc, cur) => acc + Number(cur.MAX_POINT), 0)} )
                        </td>
                        </tr>
                      )}
                    <tr>
                      <td className='content'>{item.MARK_CONTENT}</td>
                    </tr>

                    <tr>
                      <td className='score-cells d' style={{ padding: 0, margin: 0 }}>
                        {generateCellValueArray(maxPoint, item.MAX_POINT).map((value, idx) => (
                          <td 
                            className={`score-cell
                              ${Math.floor(selectedCells[item.SEQ_NO]) === value ? 'selected' : ''} 
                              ${ ( value === 0.5 && selectedCells[item.SEQ_NO]%1===0.5 ) === true ? 'selected' : ''}`}
                            onClick={()=>handleCellClick(item.SEQ_NO, value)}>
                            {value}
                          </td>
                        ))}
                     </td>
                     </tr>

                     {categories.length !== 1 && index === rowSpan-1 && (
                     <>
                      <div className = "empty_space2"></div>
                                          </>
                                          
                      )}



                    </>
                  ));
                })}
                
    <tr className="total-row">
      <td colSpan={11}>
         : {calculateTotal()} 
        { (isSave === 1) &&  " ()"}
        {( isSave=== 0 ) && " ( )"}
      </td>
    </tr>
    <div className = "empty_space"></div>
    <tr className="button-row">
      <td colSpan={2}>
        <button onClick={handleSave} className="save-button">
            
        </button>
        <button onClick={handleReset} className="reset-button">
          
        </button>
      </td>
    </tr>

          </tbody>
        </table>
                <table>
                <tbody>
                  <tr>
                    <td colSpan={2} className="button-container2">
                      { ( prev.index !== null && (rh_cd === prev.rh_cd || rh_cd !== '899000') ) ? (
                        <button
                        onClick={handleResetMove.bind(
                          null,
                          prev.link
                        )}
                          className="nav-button"
                        >
                          <div> {parseInt(detail_class_cd) % 2 === 0 ? "" : ""}</div>
                          {prev.name} ({getRoundDescription(prev.rh_cd)})
                        </button>
                      ) : (
                        <span></span>
                      )}
                      {( next.index !== null && (rh_cd === next.rh_cd || next.rh_cd !== '899000'))? (
                        <button
                          onClick={handleResetMove.bind(
                            null,
                            next.link
                          )}
                          className="nav-button"
                        >
                          <div> {parseInt(detail_class_cd) % 2 === 0 ? "" : ""}</div>
                          {next.name} ({getRoundDescription(next.rh_cd)})
              
                        </button>
                      ) : (
                        <span></span>
                      )}
                    </td>
                  </tr>
                </tbody>
              </table>
              </>
        ): (<p>Invalid refree value or no table data available.</p> )
      }
      
      


      <div className = "empty_space"></div>


    </div>
  );
}

export default NsrResult;
