import React from 'react';
import { Link, useNavigate } from 'react-router-dom';

const BackBar = ({ url, text, sentialDigit }) => {
  const backBarStyle = {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-start',
    padding: '5px',
    borderBottom: '1px solid #ccc',

  };

  const arrowStyle = {
    margin: '0',
    marginTop: '-5px',
    cursor: 'pointer',
    transform: 'scale(0.9)', //  0.9 
    transformOrigin: 'center', //    ( )
  };

  const refreeNameStyle = {
    textAlign: 'center', //  
    flexGrow: 1, // h2    flexGrow 
    marginRight: '27px', //   
  };

  const navigate = useNavigate();
  const handleResetMove = (nextLink) => {  
    if (sentialDigit) {
      const isConfirmed = window.confirm("   . ?");
      if (!isConfirmed) {
        return;
      }
    }
    if (nextLink) {
      navigate(nextLink);
    }
  };

  // sentialDigit  true  Link  
  if (sentialDigit) {
    return (
      <div style={backBarStyle} onClick={() => handleResetMove(url)}>
        <h1 style={arrowStyle}>{''}</h1>
        {text && <h3 style={refreeNameStyle}>{text}{sentialDigit}</h3>}
      </div>
    );
  }

  // sentialDigit  false  Link 
  return (
    <div style={backBarStyle}>
      <Link to={url}>
        <h1 style={arrowStyle}>{''}</h1>
      </Link>
      {text && <h3 style={refreeNameStyle}>{text}{sentialDigit}</h3>}
    </div>
  );
};

export default BackBar;
