// getRoundDescription.js

export function getRoundDescription(round) {
    //    
    if (typeof round === 'string') {
        round = parseInt(round, 10);
    }

    //     
    if (round === 891000) {
        return "";
    } else if (round === 899000) {
        return "";
    }

    //     
    const firstDigit = round % 10;
    return `${firstDigit}`;
}
